import React from 'react'
import PropTypes from 'prop-types'
import Helmet from 'react-helmet'
import { graphql, useStaticQuery } from 'gatsby'
import { Location } from '@reach/router'

const jsonLdSchema = {
  '@context': 'https://schema.org/',
  '@type': 'HowTo',
  name: 'Add JSON-LD markup with Google Tag Manager',
  description:
    'Follow these simple steps to get you JSON-LD structured data markup to work with Google Tag Manager and validate with Google Structured Data Testing Tool.',
  image:
    'https://saijogeorge.com/json-ld-schema-generator/json-ld-tag-manager-fix.png',
  totalTime: 'PT2M',
  estimatedCost: {
    '@type': 'MonetaryAmount',
    currency: 'USD',
    value: '0',
  },
  supply: {
    '@type': 'HowToSupply',
    name: 'JSON-LD Code',
  },
  tool: [
    {
      '@type': 'HowToTool',
      name: 'JSON-LD Tag Manager Fix Generator',
    },
    {
      '@type': 'HowToTool',
      name: 'Google Tag Manager',
    },
    {
      '@type': 'HowToTool',
      name: 'Google Structured Data Testing Tool',
    },
  ],
  step: [
    {
      '@type': 'HowToStep',
      text:
        'Get the JSON-LD code you want to add via Google Tag Manager, past it into the JSON-LD input section on JSON-LD Tag Manager Fix Generator.',
      image:
        'https://saijogeorge.com/json-ld-schema-generator/json-ld-tagmanager-fix-step1.gif',
      name: 'Paste the JSON-LD code into the JSON-LD input section.',
      url:
        'https://saijogeorge.com/json-ld-schema-generator/tag-manager-fix/#step1',
    },
    {
      '@type': 'HowToStep',
      text:
        'Click on Generate Tag Manager Code to generate the Google Tag Manager friendly version of the JSON-LD code.',
      image:
        'https://saijogeorge.com/json-ld-schema-generator/json-ld-tagmanager-fix-step2.gif',
      name: 'Click on Generate Tag Manager Code.',
      url:
        'https://saijogeorge.com/json-ld-schema-generator/tag-manager-fix/#step2',
    },
    {
      '@type': 'HowToStep',
      text:
        'You have the option to choose the minified or prettified code and then click on Copy Tag Manager Code button to copy the code to your clipboard.',
      image:
        'https://saijogeorge.com/json-ld-schema-generator/json-ld-tagmanager-fix-step3.gif',
      name: 'Click on Copy Tag Manager Code.',
      url:
        'https://saijogeorge.com/json-ld-schema-generator/tag-manager-fix/#step3',
    },
    {
      '@type': 'HowToStep',
      text:
        'Open Google Tag Manager and paste the code into a Custom HTML tag and fire it on all the required pages.',
      image:
        'https://saijogeorge.com/json-ld-schema-generator/json-ld-tagmanager-fix-step4.gif',
      name: 'Paste the code in Tag Manager.',
      url:
        'https://saijogeorge.com/json-ld-schema-generator/tag-manager-fix/#step4',
    },
    {
      '@type': 'HowToStep',
      text: 'Publish the new tag via Google Tag Manager.',
      image:
        'https://saijogeorge.com/json-ld-schema-generator/json-ld-tagmanager-fix-step5.gif',
      name: 'Publish it.',
      url:
        'https://saijogeorge.com/json-ld-schema-generator/tag-manager-fix/#step5',
    },
    {
      '@type': 'HowToStep',
      text:
        'Now you can test the new schema markup using the Google Structured Data Testing Tool.',
      image:
        'https://saijogeorge.com/json-ld-schema-generator/json-ld-tagmanager-fix-step6.gif',
      name: 'Validate on Google Structured Data Testing Tool.',
      url:
        'https://saijogeorge.com/json-ld-schema-generator/tag-manager-fix/#step6',
    },
  ],
}

const SEO = ({
  lang: userLang,
  title: userTitle,
  description: userDescription,
  keywords: userKeywords = [],
  facebookImage: userFacebookImage,
  twitterImage: userTwitterImage,
  meta = [],
}) => {
  const data = useStaticQuery(graphql`
    {
      site {
        siteMetadata {
          defaultLang
          defaultTitle
          defaultDescription
          defaultKeywords
          siteUrl
          siteName
          twitterCreator
          twitterSite
        }
      }
      facebookImage: file(relativePath: { eq: "share-image.jpg" }) {
        ...facebookShareImage
      }
      twitterImage: file(relativePath: { eq: "share-image.jpg" }) {
        ...twitterShareImage
      }
    }
  `)

  const {
    defaultLang,
    defaultTitle,
    defaultDescription,
    defaultKeywords,
    siteUrl,
    siteName,
    twitterCreator,
    twitterSite,
  } = data.site.siteMetadata
  const defaultFacebookImage = data.facebookImage.childImageSharp.fixed.src
  const defaultTwitterImage = data.twitterImage.childImageSharp.fixed.src

  const lang = userLang || defaultLang
  const title = userTitle ? userTitle : defaultTitle
  const description = userDescription || defaultDescription
  const keywords = userKeywords || defaultKeywords
  const facebookImage = siteUrl + (userFacebookImage || defaultFacebookImage)
  const twitterImage = siteUrl + (userTwitterImage || defaultTwitterImage)

  return (
    <Location>
      {({ location: { pathname } }) => (
        <Helmet
          htmlAttributes={{ lang }}
          title={title}
          meta={[
            { name: 'description', content: description },
            keywords.length > 0
              ? { name: 'keywords', content: keywords.join(', ') }
              : {},

            // Used by Facebook and Twitter
            { property: 'og:title', content: title },
            { property: 'og:description', content: description },
            { property: 'og:url', content: siteUrl + pathname },

            // Facebook specific
            { property: 'og:site_name', content: siteName },
            { property: 'og:locale', content: lang },
            { property: 'og:image', content: facebookImage },
            { property: 'og:type', content: 'website' },

            // Twitter specific
            { name: 'twitter:site', content: twitterSite },
            { name: 'twitter:creator', content: twitterCreator },
            { name: 'twitter:image', content: twitterImage },
            { name: 'twitter:card', content: 'summary_large_image' },

            ...meta,
          ]}
        >
          <script type="application/ld+json">
            {JSON.stringify(jsonLdSchema)}
          </script>
        </Helmet>
      )}
    </Location>
  )
}

SEO.propTypes = {
  lang: PropTypes.string,
  title: PropTypes.string,
  description: PropTypes.string,
  keywords: PropTypes.arrayOf(PropTypes.string),
  facebookImage: PropTypes.string,
  twitterImage: PropTypes.string,
  meta: PropTypes.arrayOf(PropTypes.object),
}

export default SEO
